<template>
  <div>
    <div style="height:200px;">
    </div>
        <div class="row">
          <div class="col-12">
            <div class="p-2 shadow-lg  bg-white">
                <div class="row">
                    <div class="col-12">
                        <div class="p-3">
                            <!-- <v-select :options="kategori" label="nama" v-model="kategoris"></v-select> -->
                            <select class='form-control' v-model="kategoris" placeholder='kategori'>
                                <option value="">-- Kategori --</option>
                            <option v-for="(item, index) in kategori" :key="index">{{item.kategori}}</option>
                            </select>
                            <div class="sm-form ">
                                <input type="text" id="search" name="search" class="form-control form-control-sm" placeholder="search ..." v-model="search" >
                            </div>
                        </div>
                    </div>
                    <div class="col-6 mt-3" v-for="(item, index) in items" :key="index+'key'">
                        <img :src="item.gambar.length > 10 ? item.gambar :require('@/static/nofound2.png')" class="img-thumbnail" v-viewer alt="" style="height:130px;width:100%;display:block;margin:auto;">
                        <div class="rounded-lg shadow-lg p-2">
                            <p class="font-bold">{{item.nama_makanan}}</p>
                            <p class="float-right">Stok : {{item.jumlah}}</p>
                            <p class="font-bold font-italic">Rp.{{item.harga}}</p>
                            <div v-if="item.jumlah>0">
                                <button type="button" @click="tambah(index);" class="btn btn-sm btn-style9 btn-block" style="background:green;">Tambah +</button>
                                <button v-if="item.total>0" type="button" @click="kurang(index);" class="btn btn-sm btn-style9 btn-block" style="background:red;">Hapus</button>
                            </div>
                            <div v-else>
                                <button type="button" @click="tambah(index);" class="btn btn-sm btn-style9 btn-block" style="background:grey;">HABIS</button>
                            </div>
                            <p v-if="item.total>0">Total : {{item.total}}</p>
                            
                        </div>
                    </div>
                    <div class="col-12">
                        <br>
                            <br>
                            <br>
                    </div>
                    <div class="col-12">
                        <button type="button" @click="$router.push('/cart')" class="btn btn-sm btn-style9 btn-block" style="position:fixed;bottom:0;left:0;">Keranjang ! <span class="typcn typcn-shopping-cart"></span></button>
                    </div>
                </div>
            </div>
          </div>
      </div>
  </div>
</template>
<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
// let rdb = firebase.database()
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
  data(){
    return{
        datanya:[],
        total:0,
        kategori:[],
        kategoris:'',
        search:"",
        foods:[
            {
                img:"@/static/gambar1.jpg"
            }
        ],
    }
  },
  computed:{
      items(){
          let data = this.$store.state.cart
          data = data.filter(e=>{
              if(e.nama_makanan.toLowerCase().indexOf(this.search.toLowerCase())!=-1){
                  return e
              }
          })
          data = data.filter(e=>{
              if(e.kategori.toLowerCase().indexOf(this.kategoris.toLowerCase())!=-1){
                  return e
              }
          })
          return data
      }
  },
  methods:{
      getKategori(){
          db.collection('kategori').onSnapshot(res=>{
              let data = res.docs.map(e=>e.data())
              this.kategori = data
          })
      },
      getData(){
          db.collection('food').onSnapshot(res=>{
              let data = res.docs.map(e=>{
                  return{
                      id:e.id,
                      total:0,
                      ...e.data()
                  }
              })
              this.datanya = data
              this.$store.state.cart = data
            //   console.log(data)
          })
      },
      tambah(item){
          if(this.$store.state.cart[item].total<this.$store.state.cart[item].jumlah){
              this.$store.state.cart[item].total++;
          }
          console.log(this.$store.state.cart[item])
      },
      kurang(item){
          if(this.$store.state.cart[item].total>0){
              this.$store.state.cart[item].total=this.$store.state.cart[item].total-1;
          }
      },
      checkout(){
          let data = this.datanya; 
          data=data.filter(e=>{
              if(e.total>0){
                  return e
              }
          })
          if(confirm("Apakah sudah yakin dengan orderan ?")){
              this.$store.state.cart = data
              console.log(data)
            this.$router.push('/cart');

            }
      }
  },
  mounted() {
    this.getKategori();
  },
  created(){
      this.getData()
  },    
  layout:"admin"
}
</script>